
import React, { useState, useContext } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import login from '../Components/Media/login.svg'
import { BsArrowLeft, BsArrowRight } from 'react-icons/bs'
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import CitizenApplyApiList from './CitizenApplyApiList';
import { RotatingLines } from "react-loader-spinner";
import { allowNumberInput } from './Common/PowerUps/PowerupFunctions';
import { contextVar } from './ContextVar';
import BackendUrl from './ApiList/BackendUrl';
import MobileNoCard from '../Pages/Property/CitizenAuth/MobileNoCard';
import OtpCard from '../Pages/Property/CitizenAuth/OtpCard';
import CommonModal from './GlobalData/CommonModal';
import ServerErrorCard from '../Pages/AdvertisementModule/ServerErrorCard';


const LoginForm = () => {
    console.log('backend url imported....', BackendUrl)
    const [loaderStatus, setLoaderStatus] = useState(false)
    const [mobileCardStatus, setmobileCardStatus] = useState(false)
    const [otpCardStatus, setotpCardStatus] = useState(false)
    const { api_citizenLogin2 } = CitizenApplyApiList()
    const [verifedMobileNo, setverifedMobileNo] = useState(null)

    const { notify } = useContext(contextVar)

    const navigate = useNavigate();
    const [showMsg, setShowMsg] = useState()
    const [reset, setreset] = useState(true)

    const validationSchema = Yup.object().shape({


        mobile: Yup.string().required('Enter mobile no. !').min(10).max(10),
        password: Yup.string()
            .min(6, 'Minimum six character !')
            .max(50, 'Too Long!')
            .required('Enter password no. !')
            .matches(/^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z]).{6,}$/, 'Password Must Contains (Capital, Smail, Number, Special) eg - Abc123#.'),
    });
    const initialValues = {
        mobile: '',
        password: '',

    }

    const formik = useFormik({
        initialValues: initialValues,

        onSubmit: (values) => {
            console.log("post data", values)
            handleLogin(values)
        },
        validationSchema
    });

    const handleLogin = (values) => {

        setShowMsg('');
        setLoaderStatus(true)
        seterroState(false)
        axios
            .post(api_citizenLogin2, {
                mobile: values?.mobile,
                password: values?.password
            })
            .then((res) => {

                console.log('response after login....', res?.data)
                if (res?.data?.status === true) {
                    localStorage.setItem("citizen_token", res?.data?.data?.token)
                    setLoaderStatus(false)
                    localStorage.setItem('propertyPopup', true);
                    notify('Login Success', 'success')
                    //SETTING USER DATA INTO LOCALSTORAGE FOR FURTHER USER
                    window.localStorage.setItem('citizenMobile', res?.data?.data?.mobile)
                    window.localStorage.setItem('citizenName', res?.data?.data?.userName)
                    navigate("/landingDashboard")
                } else {
                    console.log("Login Failed", res?.data?.message)
                    notify(res?.data?.message, 'error')
                    setLoaderStatus(false)
                }
                console.log("=== Invalid Details", res)
                setShowMsg(res?.data?.message)
            })
            .catch(err => {
                console.log(err)
                console.log("-Exception--", err)
                setLoaderStatus(false)
                setShowMsg("something went wrongg")
                seterroState(true)
                notify('something went wrongg !!', 'error')

            })
    }

    const handleChangeInput = (event) => {
        let name = event.target.name
        let value = event.target.value

        { name == 'mobile' && formik.setFieldValue("mobile", allowNumberInput(value, formik.values.mobile, 10)) }
    }
    const callback = () => {
        notify('Mobile no. has been verified successfully !', 'success')
        navigate('/change-password/forgot')
    }

    const closeMobileModal = () => {
       setmobileCardStatus(false)
      }
    const closeOtpModal = () => {
        setotpCardStatus(false)
    }

    const [erroState, seterroState] = useState(false);
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    if (mobileCardStatus) {
        return (
            <MobileNoCard closeMobileModal={closeMobileModal} headTitle="Forgot Password" title="Enter mobile to get OTP" desc="Enter your mobile no, which is attached to your account." setverifedMobileNo={setverifedMobileNo} setmobileCardStatus={setmobileCardStatus} setotpCardStatus={setotpCardStatus} />
        )
    }
    if (otpCardStatus) {
        return (
            <OtpCard closeOtpModal={closeOtpModal} bottomNaviation={true} headTitle="Forgot OTP" callback={callback} setotpCardStatus={setotpCardStatus} />
        )
    }


    return (
        <>
            <CommonModal>
                <div className='w-full text-center mx-auto bg-white md:bg-none shadow-lg md:shadow-none'>
                    <div className='w-full md:w-3/5 mx-auto mt-16 rouned-lg '>
                        <form onChange={handleChangeInput} onSubmit={formik.handleSubmit}>
                            <div className='grid grid-cols-12 px-2 md:px-0 shadow-none md:shadow-lg '>
                                <div className='col-span-6 hidden md:block bg-white md:py-10'><img className='w-3/4' src={login} /></div>
                                <div className='w-full md:px-10 col-span-12 md:col-span-6 md:shadow-none bg-white py-10'>
                                    <div className='w-full  pb-6 pt-0 md:py-6 md:mt-10 text-gray-700'>
                                        <h1 className='text-center text-md font-semibold md:my-1 capitalize w-full'>
                                        </h1>
                                        <h1 className='text-center font-semibold my-1 flex justify-center'>
                                            <span className='px-1 text-semibold text-2xl -mt-5 mb-4'>Log In </span>
                                        </h1>

                                        <div className='my-3 relative text-sm'>
                                            <div className='text-gray-600 static mb-1 font-semibold text-left px-8 md:px-10 '>Mobile No.<span className='text-red-500 font-bold'>*</span></div>
                                            <input
                                                type="text"
                                                min={0}
                                                {...formik.getFieldProps('mobile')}
                                                className='cypress_user_mobile border-b border-b-gray-200 focus:border-b focus:border-b-indigo-500  pl-2 px-3 py-1 w-5/6 shadow-sm h-10 outline-none border border-solid border-gray-300 rounded hover:bg-gray-100'
                                                placeholder="Enter Your mobile"
                                            />
                                            <div>
                                                <span className="text-red-600 absolute text-xs">{formik.touched.mobile && formik.errors.mobile ? formik.errors.mobile : null}</span>
                                            </div>
                                        </div>

                                        <div className='my-3 relative text-sm mt-4'>
                                            <div className='text-gray-600 static mb-1 font-semibold text-left px-8 md:px-10'>Password <span className='text-red-500 font-bold'>*</span></div>
                                            <input
                                                type="password"
                                                {...formik.getFieldProps('password')}
                                                className='cypress_user_password border-b border-b-gray-200 focus:border-b focus:border-b-indigo-500  pl-2 px-3 py-1 w-5/6 shadow-sm h-10 outline-none border border-solid border-gray-300 rounded hover:bg-gray-100'
                                                placeholder="Enter Your Password"
                                            />
                                            <div>
                                                <span className="text-red-600 absolute text-xs">{formik.touched.password && formik.errors.password ? formik.errors.password : null}</span>
                                            </div>
                                        </div>
                                        <div className=' my-10'>
                                        </div>
                                        <div className='text-center mt-3 text-xs'>

                                        </div>
                                        <div className='my-5 self-center justify-self-center relative'>
                                            <div className='text-red-600 text-sm font-semibold my-3 '> <span className=''> {showMsg}</span></div>

                                            {loaderStatus ?
                                                <div className='flex justify-center'>
                                                    <RotatingLines
                                                        strokeColor="grey"
                                                        strokeWidth="5"
                                                        animationDuration="0.75"
                                                        width="25"
                                                        visible={true}
                                                    />
                                                </div>
                                                : <button
                                                    type="submit"
                                                    disabled={loaderStatus}
                                                    className='cypress_login bg-indigo-500 hover:bg-indigo-700 px-5 shadow-xl py-2 w-72  rounded-md text-white text-lg font-semibold'>
                                                    Login
                                                </button>
                                            }
                                        </div>
                                        <div className='flex flex-row flex-wrap gapx-x-2 gap-y-2 w-full poppins justify-center'>
                                            <span className='text-gray-700 text-sm font-semibold cursor-pointer md:w-[40%] w-full' onClick={() => setmobileCardStatus(true)}>Forgot Password</span>
                                        </div>
                                        <div className='text-left w-full mt-10'>
                                            <span onClick={() => navigate('/')} className='text-indigo-500 font-semibold hover:bg-white px-3 py-1 cursor-pointer '><BsArrowLeft className="inline mr-2" />Home</span>
                                            <span onClick={() => navigate('/register')} className='text-indigo-500 font-semibold hover:bg-white px-3 py-1 cursor-pointer float-right'>Register <BsArrowRight className="inline ml-2" /></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </CommonModal>

        </>
    );
}

export default LoginForm